export default {
  name: 'History',

  props: {
    historyData: {
      type: Array,
      required: true,
    },
    historyType: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
  },

  data: () => ({
    columns: null,
    withdrawColumns: null,
    loading: false,
    expandedRows: [],
  }),

  beforeMount() {
    this.columns = [
      {
        title: 'Birim',
        dataIndex: 'Asset',
        align: 'left',
        width: '15%',
        scopedSlots: { customRender: 'Asset' },
      },
      {
        title: 'Durum',
        dataIndex: 'Status',
        width: '10%',
        align: 'left',
        scopedSlots: { customRender: 'Status' },
      },
      {
        title: 'Miktar',
        dataIndex: 'Amount',
        width: '10%',
        align: 'left',
      },
      {
        title: 'Adres',
        dataIndex: 'Address',
        width: '35%',
        align: 'left',
        scopedSlots: { customRender: 'Address' },
      },
      {
        title: 'Tarih',
        width: '25%',
        align: 'left',
        scopedSlots: { customRender: 'Time' },
      },
      {
        title: '',
        dataIndex: '',
        width: '5%',
        align: 'left',
      },
    ]
  },
  methods: {
    handleTableChange(pagination, filters, sorter) {
    },

    getTime(val) {
      return new Date(val).toUTCString()
    },
    onExpandedRowsChange(expandeds) {
      this.expandedRows = expandeds
    },
    onExpandRow(expanded, record) {
      this.expandedRows = []
      if (expanded) this.expandedRows.push(this.historyData.indexOf(record))
    },
    expandIcon(props) {
      const text = (
        <span>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="11"
            height="6"
            viewBox="0 0 11 6"
          >
            <g
              id="Group_5807"
              data-name="Group 5807"
              transform="translate(-1839 -531)"
            >
              <path
                id="Polygon_17"
                data-name="Polygon 17"
                d="M5.5,0,11,6H0Z"
                transform="translate(1850 537) rotate(180)"
                fill="#858e9d"
              />
            </g>
          </svg>
        </span>
      )

      return (
        <a
          class="expand-row-icon"
          onClick={(e) => props.onExpand(props.record, e)}
          style={{ color: 'blue', cursor: 'pointer' }}
        >
          {text}
        </a>
      )
    },
  },
}
