import VueQrcode from '@chenfengyuan/vue-qrcode'
import { mapGetters } from 'vuex'
import IconBulb from '@/components/Icons/IconBulb.vue'
export default {
  name: 'NetworkTab',
  components: {
    VueQrcode,
    IconBulb,
  },

  props: {
    name: {
      type: String,
      required: false,
    },
    address: {
      type: String,
      required: false,
    },
    memo: {
      type: String,
      required: false,
    },
    enabled: {
      type: Boolean,
      required: true,
    },
  },

  computed: {
    ...mapGetters({
      SELECTED_ASSET: 'mainwallet/SELECTED_ASSET',
    }),
  },

  methods: {
    handleSuccess(e) {
    },
    handleError(e) {
    },
  },
}
